@import "../../../../../assets/scss/common.scss";

.service {
  border-right: 2px solid #d2d3d4;
  max-width: 200px;
  .input {
    @extend .B4-Semi-bold;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-family: Inter;
    color: #090909;
    padding: 0 8px;
  }
}

.table {
  border-radius: 16px 16px 0px 0px;
  overflow: hidden;
  background-color: #E8E9E9;
}

.iconbtn {
  &:hover {
    .circleAdd {
      color: #391765 !important
    }
  }
  .circleAdd {
    color: #935ed6;
    width: 18px;
    height: 18px;
  }

  .deleteicon {
    width: 15px;
    height: 17px;
    margin-left: 1px;
  }
}

.tableWrap {
  //   border-bottom: 1px solid #c8c8c8;
  margin-top: 16px;
  thead {
    tr {
      th {
        background: #e2f8fb;
        @extend .B3-Medium;
        color: #2a5067;
        padding: 14px 8px;
        white-space: nowrap;
        .wrapth {
          display: flex;
          align-items: center;
          .checkbox {
            margin-right: 8px;
          }
        }
      }
    }
  }
  tbody {
    border-left: 1px solid #e8e9e9;
    border-right: 1px solid #e8e9e9;

    tr {
      td {
        padding: 0;
        border-color: #f6f6f6;
        position: relative;
        .mt--4{
          margin-top: -3px !important;
        }
      }
    }
  }
}

.notapplicable {
  padding: 0 15px 0 15px;
  min-width: 144px;
  > div {
      padding: 15px 14px 0 10px;
      line-height: 18px;
      color: #090909;
      font-size: 12px;
      line-height: 19px;
      font-family: Inter;
      font-weight: 400;
  }
}

.reactselect {
  width: 100%;
  :global {
    .Select__control {
      max-width: 100%;
      border: 0 !important;
      min-width: 138px;
      box-shadow: unset;
      padding-right: 0;
      border-radius: 0;
      min-height: 45px;
      width: 100%;
      font-family: "Barlow" !important;
      font-weight: 600;   
      font-size: 14px !important;

      &.Select__control--menu-is-open {
        border: 1px solid #245675 !important;
      }

      .Select__value-container {
        padding-left: 8px;
        .Select__single-value {
          font-size: 12px;
          font-weight: 600;
          line-height: 18px;
        }
        .Select__placeholder {
          font-size: 12px;
          font-weight: 400;
          line-height: 18px;
          font-family: Inter;
        }
      }

      .Select__indicators {
        .Select__indicator {
          border-right: 1px solid#D2D3D4;
          padding: 0;
          padding-right: 12px;
        }
        .Select__dropdown-indicator {
          color: #1e2528;
        }

        .Select__indicator-separator {
          display: none;
        }
      }
    }

    .Select__menu {
      top: unset;
      width: 200px;
      .Select__menu-list {
        .Select__option {
          background-color: transparent !important;
          font-family: Barlow;
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          letter-spacing: 0em;
          cursor: pointer
        }
      }
    }
  }
}

.noIcons {
  width: 100%;
  :global {
    .Select__control {
      font-family: "Barlow";
      min-height: 45px;
      min-width: 138px;
      border: 0 !important;
      box-shadow: unset;
      padding-right: 0;
      border-radius: 0;
      font-size: 12px;
      font-weight: 600;
      line-height: 18px;
      letter-spacing: 0em;
      &.Select__control--menu-is-open {
        border: 1px solid #245675 !important;
      }
      .Select__value-container {
        padding-left: 8px;
        border: 0 !important;
      }

      .Select__indicators {
        .Select__indicator {
          padding: 0;
          padding-right: 12px;
          border-right: none;
        }
        .Select__indicator-separator {
          display: none !important;
        }
      }
    }

    .Select__menu {
      top: unset;
      .Select__menu-list {
        .Select__option {
          background-color: transparent !important;
          font-family: "Barlow";
          font-size: 14px;
          font-weight: 500;
          line-height: 20px;
          letter-spacing: 0em;
        }
      }
    }
  }
}

.input {
  @extend .B4-Semi-bold;
  border: 0;
  border-radius: 0;
  min-width: 144px;
  padding: 0 8px;
  &::placeholder {
    font-size: 12px;
    font-weight: 500;
    font-family: Inter;
    color: #a5a8a9;
  }

  &:focus {
    border: 1px solid#090909 !important;
  }
}

.padding {
  padding: 13px 8px !important;
}
.checkbox {
  margin-top: 0;
}

.action {
  padding: 13.5px 16.5px;
  vertical-align: middle;
  border-left: 1px solid #f6f6f6;
}
